.navbar_header {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #333;
  color: #fff;
  height: 55px;
  box-shadow: 0 5px 15px 0 rgba(17, 17, 17, 0.377);
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.logo {
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
}
.indicatorBox {
  display: flex;
  align-items: center;
  gap: 1rem;

  > small {
    text-transform: capitalize;
  }
}
.Connection_Indicator {
  display: inline-block;
  height: 15px;
  width: 15px;
  background-color: gray;
  box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.336);
  border-radius: 50%;
  margin: auto 0;
  transition: background-color 0.7s ease-out;
}
