.modalBody {
  position: fixed;
  z-index: 1005;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f6f8f85e;
}

.modalBackdrop {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}
.modalContent {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  width: 70%;
  transition: transform 0.7s ease-out, opacity 0.3s ease-out;
  transform: translateY(-80px);
  animation-name: fadeInput;
  animation-duration: 0.7s;
  animation-timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28);

  @media (max-width: 768px) {
    width: 95%;
  }

  > input {
    padding: 1.1rem;
    border-radius: 0.5rem;
    text-transform: capitalize;
    font-weight: bold;
    @media (max-width: 768px) {
      padding: 0.5rem;
    }
  }

  > button {
    @media (max-width: 768px) {
      padding: 0.5rem !important;
    }
  }
}

.closeModal {
  position: absolute;
  top: 60px;
  left: 15px;
  border: 1px solid;
  background-color: #f66;
  padding: 0.5rem;
  opacity: 0.7;
}

@keyframes fadeInput {
  0% {
    transform: translateY(-105px);
  }

  100% {
    transform: translateY(-80px);
  }
}
