.createTaskBTN {
  display: flex;
  flex-direction: row;
  gap: 15px;
  position: fixed;
  z-index: 10;
  top: 90%;
  left: 50%;
  width: 190px;
  padding: 0.5rem 1rem;
  border-radius: 55px;
  border: none;
  background-color: #43919b;
  color: #fff;
  transform: translateX(-50%);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  transition: background-color 0.3s ease-out;

  &:hover {
    background-color: #f8f8f6;
    border: 1px solid #00092c;
    color: #00092c;
  }

  > p {
    color: #00092c;
    font-weight: bold;
    text-transform: uppercase;
    margin: auto;
  }
  > :nth-child(2) {
    font-size: 1.5rem;
  }
}
