.todosList {
  display: grid;
  grid-auto-rows: min-content;
  grid-auto-flow: row;
  margin-top: 2rem;
  margin-bottom: 2rem;
  max-height: 40vh;
}

.todo_card {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 1rem;
  padding: 0 0.5rem;
  transition: border 0.5s ease-out;
  background-color: #be8c63;

  @media (max-width: 768px) {
    flex-direction: column;
    height: fit-content;
  }
  > input {
    @media (max-width: 768px) {
      height: 1.5rem;
    }
  }
}

.todo_Content {
  position: relative;
  z-index: 5;
  height: 100%;
  background-color: #43919b;
  flex-grow: 1;
  padding: 0.5rem 1rem;
  border-radius: 0.375rem;
  overflow: hidden;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);

  @media (max-width: 768px) {
    width: 100%;
    padding: 0.5rem 0;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    top: 50%;
    height: 100%;
    width: 8px;
    background-color: #00092c;
    transform: translateY(-50%);
    border-radius: 45px;
    transition: width 0.5s ease-out;
  }

  &:hover > h5,
  &:hover > p {
    color: #f6f6f6 !important;
  }
  &:hover::after {
    width: 100%;
    border-radius: 0;
  }
  h5 {
    text-transform: capitalize;
    color: #00092c;
    transition: color 0.5s ease;
  }
  p {
    color: #00092c;
    opacity: 0.7;
    transition: color 0.5s ease;
    margin: 0;
  }
}
