.modalBody {
  position: fixed;
  z-index: 1005;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f6f8f85e;
}

.modalBackdrop {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 100%;
  height: 100%;
}

.modalContent {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  gap: 1rem;
  width: 70%;
  -webkit-transition: opacity 0.3s ease-out, -webkit-transform 0.7s ease-out;
  transition: opacity 0.3s ease-out, -webkit-transform 0.7s ease-out;
  transition: transform 0.7s ease-out, opacity 0.3s ease-out;
  transition: transform 0.7s ease-out, opacity 0.3s ease-out, -webkit-transform 0.7s ease-out;
  -webkit-transform: translateY(-80px);
          transform: translateY(-80px);
  -webkit-animation-name: fadeInput;
          animation-name: fadeInput;
  -webkit-animation-duration: 0.7s;
          animation-duration: 0.7s;
  -webkit-animation-timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28);
          animation-timing-function: cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

@media (max-width: 768px) {
  .modalContent {
    width: 95%;
  }
}

.modalContent > input {
  padding: 1.1rem;
  border-radius: 0.5rem;
  text-transform: capitalize;
  font-weight: bold;
}

@media (max-width: 768px) {
  .modalContent > input {
    padding: 0.5rem;
  }
}

@media (max-width: 768px) {
  .modalContent > button {
    padding: 0.5rem !important;
  }
}

.closeModal {
  position: absolute;
  top: 60px;
  left: 15px;
  border: 1px solid;
  background-color: #f66;
  padding: 0.5rem;
  opacity: 0.7;
}

@-webkit-keyframes fadeInput {
  0% {
    -webkit-transform: translateY(-105px);
            transform: translateY(-105px);
  }
  100% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px);
  }
}

@keyframes fadeInput {
  0% {
    -webkit-transform: translateY(-105px);
            transform: translateY(-105px);
  }
  100% {
    -webkit-transform: translateY(-80px);
            transform: translateY(-80px);
  }
}
