.clear_all {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 0.5rem 1rem;
  background-color: #be8c63;
  border: 1px solid #00092c;
  color: #fff;
  border-radius: 0.375rem;
  -webkit-transition: opacity 0.3s, background-color 0.3s ease-out;
  transition: opacity 0.3s, background-color 0.3s ease-out;
}

.clear_all:hover {
  opacity: 0.9;
  background-color: #79451b;
}

.clear_all > span {
  font-size: 1.5rem;
}

.clear_all > p {
  margin-bottom: auto;
  margin-top: auto;
  text-transform: uppercase;
  font-weight: bold;
}
