.clear_all {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 0.5rem 1rem;
  background-color: #be8c63;
  border: 1px solid #00092c;
  color: #fff;
  border-radius: 0.375rem;
  transition: opacity 0.3s, background-color 0.3s ease-out;

  &:hover {
    opacity: 0.9;
    background-color: #79451b;
  }
  > span {
    font-size: 1.5rem;
  }
  > p {
    margin-bottom: auto;
    margin-top: auto;
    text-transform: uppercase;
    font-weight: bold;
  }
}
